*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}

body{
    background: linear-gradient(90deg, blue, red);
}

.container{
    width: 100%;
    margin: 100px auto 10px;
    justify-content: center;
    display: flex;

}

.row{
    justify-content: center;
    display: flex;
    border: 0;
    border-radius: 24px;
    box-shadow: 5px 7px 29px black;
    padding: 20px;
    background: linear-gradient(45deg , rgba(0, 255, 195, 0.712), rgb(183, 0, 255));
}

.row h1{
    text-align: center;
    width: 100%;
    font-size: 34px;
    font-weight: 600;
    margin: 16px 0;
    color: white;
}

.row input{
    width: 100%;
    height: 47px;
    border: 0;
    border-radius: 20px;
    font-size: 24px;
    font-weight: 500;
    padding: 0 19px;
    margin: 14px 0;
    box-shadow: 6px 10px 12px black;
    
}

.error{
    animation: shake .1s linear 10;
}

.row button{
    width: 80%;
    height: 43px;
    border-radius: 10px;
    margin: 10px auto;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 24px;
    font-weight: 600;
    color: white;
    background: rgb(199, 26, 226);
    border: 0;
    box-shadow: 3px 5px 10px white;
}

.QR{
    width: 60%;
    margin: 13px auto;
    display: none;
    justify-content: center;
    color: white;
    overflow: hidden;
    border: 2px solid brown;
    padding: 3px;
    
}

.QR img{
    width: 100%;
    object-fit: cover;

}

@keyframes shake {
    0%{
        transform: translateX(0);
    }

    25%{
        transform: translateX(-2px);
    }
    50%{
        transform: translateX(0);
    } 
    75%{
        transform: translateX(2px);
    }
    
    100%{
        transform: translateX(0);
    }
}